/* 초기화 */
* {
  box-sizing: border-box;
}
/* 눈누에서 가져온 에스코어드림 폰트 -> body에 적용 필요 */
@font-face {
  font-family: 'S-CoreDream-3Light';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'TTWanjudaedunsancheB';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-2@1.0/TTWanjudaedunsancheB.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

:root {
  --gray100: #F2F2F2;
  --gray200: #D9D9D9;
  --gray300: #808080;
  --gray400: #333333;
  --gray500: #262626;
  --gray600: #1A1A1A;
  --gray700: #0D0D0D;
  --purple: #8284FA;
  --purpledark: #5E60CE;
  --blue: #4EA8DE;
  --bluedark: #1E6F9F;
  --danger: #E25858;
  --mw : 800px;
}

a {
  text-decoration: none;
  color: inherit;
}
/* 초기화 */

body {
  background-color: var(--gray600);
  color: var(--gray100);
  font-family: 'S-CoreDream-3Light';
}

header {
  height: 200px;
  background-color: var(--gray700);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  font-family: 'TTWanjudaedunsancheB';
}
/* 로켓 가상태그 */
header h1::before{
  content: "\f135";
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  color: var(--purpledark);
  display: inline-block;
  transform: rotate(-45deg) translate(-4px, 4px);
}
header h1 span:nth-of-type(1){
  color: var(--blue);
}
header h1 span:nth-of-type(2){
  color: var(--bluedark);
}

/* 헤더 끝 */

main {
  max-width: var(--mw);
  width: calc(100% - 4rem);
  margin: 0 auto;
}

.textinput {
  display: flex;
  gap: 1rem;
  position: relative;
  top: -1.5rem;
}

.textinput input {
  background-color: var(--gray500);
  color: var(--gray100);
  flex-grow: 1;
}

.textinput input:focus {
  background-color: var(--gray400);
}


.textinput button {
  background-color: var(--blue);
  color: var(--gray100);
}

.textinput button::after {
  content: "\f055";
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  color: var(--gray100);
  padding: 0.2em 0.5em;
}

.textinput button:hover {
  background-color: var(--bluedark);
}

.textinput input, .textinput button {
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  border : none;
  outline: none;
}

@media (max-width : 500px) {
  .textinput {
      flex-direction: column;
  }
}

/*  */

.total {
  text-align: right;
  font-size: 0.75rem;
  color: var(--purpledark);
  margin-bottom: 1rem;
}

.total span {
  display: inline;
  background-color: var(--gray300);
  color: var(--gray100);
  padding: 0.2em 0.5em;
  border-radius: 50%;
}


/*  */


.list li{
  border: 1px solid var(--gray400);
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  background-color: var(--gray500);
  margin-bottom: 1rem;
}
/* 길어졌을 때 표시 // 줄바꿈 X ... 표시 */
.list li p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 1rem 2rem;
}

.list li i {
  display: inline-block;
  padding: 1rem 1rem;
  transition: 0.5s;
}

.list li i:hover {
  color: var(--danger);
}

.Popup {
  /* background-color: var(--gray100); */
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  padding: 3rem;
  color: var(--gray700);

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}